.spinner {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .ball {
    width: 20px;
    height: 20px;
    margin: 10px;
    border-radius: 50%;
    background-color: #333;
    animation: bounce 0.5s ease-in-out infinite alternate;
  }
  
  .ball1 {
    animation-delay: -0.4s;
  }
  
  .ball2 {
    animation-delay: -0.2s;
  }
  
  @keyframes bounce {
    to {
      transform: translateY(-20px);
    }
  }
  